import React from 'react';
import {observer} from 'mobx-react';
import {Form, Input, InputNumber, Row, Col} from 'antd';

const {TextArea} = Input;


@observer
class Technological extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {

	}

	componentWillUnmount() {

	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	handLeChange = ({ target: { value } }) => {
		this.props.updateState(value);
	};

	render() {
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 18},
			},
		}
		const fields = this.props.expect;
		const {getFieldDecorator} = this.props.form;
		const {disabled} = this.props;

		return (
			<Form {...formItemLayout}>
				<Form.Item label=""
				           labelCol={{
					           xs: {span: 24},
					           sm: {span: 2},
				           }}
				           wrapperCol={{
					           xs: {span: 24},
					           sm: {span: 21},
				           }}
				           style={{marginTop: 10}}>
					{getFieldDecorator('technology', {
						initialValue: fields.technology,
						rules: [{required: false, message: ''}],
					})(<TextArea
						disabled={disabled}
						onChange={this.handLeChange}
						value={fields.technology}
						autosize={{minRows: 4}}/>)}
				</Form.Item>
			</Form>
		)
	}
}

const form = Form.create({})(Technological);
export default form;




