import React from 'react';
import {observer} from 'mobx-react';
import {Breadcrumb, Form, Row, Col, Button, Tabs, Table, Input, DatePicker, InputNumber, message, ConfigProvider,} from 'antd';
import ScheduleStore from "store/weekNews/ScheduleStore";
import LeftTree from "components/weekNews/LeftTree";
import ProEditTable from "components/weekNews/ProEditTable";
import Technological from "components/weekNews/Technological";
import moment from 'moment';
import url from 'url';
import * as mobx from "mobx";

const {TabPane} = Tabs;
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			delete values["beginTime"];
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	};
	//
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			handleSave({...record, ...values});
		});
	}
	//
	disabledStartDate = startValue => {
		const {record} = this.props;
		if (!startValue || !record.endTime) {
			return false
		}
		return startValue.valueOf() > record.endTime.valueOf()
	}
	//
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() <= record.beginTime.valueOf()
	}
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'beginTime') { // 
			// 
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing && record.subExpectResources == undefined ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate}
						//
						disabledDate={this.disabledStartDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring).format("YYYY-MM-DD")}
				</div>
			);
		} else if (dataIndex == 'endTime') { // 
			// 
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing && record.subExpectResources == undefined ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate2}
						//
						disabledDate={this.disabledEndDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring).format("YYYY-MM-DD")}
				</div>
			);
		} else if (dataIndex == 'quantities') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							style={{width: '177px'}}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else {   // 
			return editing && record.subExpectResources == undefined ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							style={{width: '177px'}}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder=""
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}


const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);


@observer
class Schedule extends React.Component {
	constructor(props) {
		super(props);
		this.store = new ScheduleStore()
		this.state = {
			key: "1",
			rowId: "",
			rowObj: {}, // 
		}
	}

	componentWillMount() {

	}

	componentDidMount() {
		let search = this.props.location.search;
		const projectId = url.parse(search, true).query.projectId;
		const parojectName = url.parse(search, true).query.name;
		if (projectId !== undefined) {
			this.store.projectId = projectId;
			this.store.parojectName = parojectName;
			localStorage.setItem("projectId", projectId);
			localStorage.setItem("projectName", parojectName);
			this.store.getDirectoryTreeByProjectId();
		} else {
			this.store.projectId = localStorage.getItem("projectId");
			this.store.parojectName = localStorage.getItem("projectName");
			this.store.getDirectoryTreeByProjectId();
		}
	}

	componentWillUnmount() {

	}

	callback = (key) => {
		this.setState({
			key
		}, () => {
			if (key == "2") {
				this.scrollToAnchor("ziyuan");
			}
		});
	}

	handleSave = (row) => {
		let expect = JSON.parse(JSON.stringify(this.store.expect));
		const {subExpectResources} = row;
		if (subExpectResources == undefined) { // 
			const {quantities, units, beginTime, endTime} = row;
			let expectSource = JSON.parse(JSON.stringify(this.store.expectSource));
			const index = expectSource.findIndex(item => row.id === item.id);
			const item = expectSource[index];
			let newRow = Object.assign(row, {"quantities": typeof row.quantities == "string" ? row.quantities : row.quantities.toString()});
			expectSource.splice(index, 1, {
				...item,
				...newRow
			});
			//
			this.store.expectSource = Object.assign([], expectSource);
			this.store.expect = Object.assign(expect, newRow);
			if (quantities !== null && quantities !== "" && units !== null && units !== "" && beginTime !== "" && beginTime !== null && endTime !== null && endTime !== "") { // 
				const param = JSON.parse(JSON.stringify(this.store.expect));
				const params = Object.assign(param, {id: this.store.id})
				this.store.saveExpect(params).then((res) => {
					if (res) {
						this.store.getExpectById();
						this.setState({
							rowId: row.id,
							rowObj: row
						}, () => {
							this.store.expectResource = Object.assign([], expect.resources == null ? [] : expect.resources);
						});
					}
				});
			}
		} else {		// 
			let expectSource = JSON.parse(JSON.stringify(this.store.expectSource));
			const index = expectSource.findIndex(item => row.id === item.id);
			const item = expectSource[index];
			let newRow = Object.assign(row, {"quantities": typeof row.quantities == "string" ? row.quantities : row.quantities.toString()});
			expectSource.splice(index, 1, {
				...item,
				...newRow
			});
			//
			this.store.expectSource = Object.assign([], expectSource);
			this.store.expect = Object.assign(expect, {
				"subExpects": expectSource.filter((item) => item.subExpectResources !== undefined)
			});
		}
	}

	handleAddd = () => {
		this.store.addDirectoryTree().then((res) => {
			if (res) {
				message.success("");
				this.store.getDirectoryTreeByProjectId();
			}
		});
	}

	// 
	onClickRow = (record) => {
		return {
			onClick: () => {
				this.setState({
					rowId: record.id,
					rowObj: record
				}, () => {
					const {subExpectResources} = record;
					if (subExpectResources == undefined) { // 
						let expect = JSON.parse(JSON.stringify(this.store.expect));
						this.store.expectResource = Object.assign([], expect.resources == null ? [] : expect.resources);
					} else {  // 
						this.store.expectResource = Object.assign([], record.subExpectResources == null ? [] : record.subExpectResources);
						this.onReset();
					}
				});
			},
		};
	}

	setRowClassName = (record) => {
		return record.id === this.state.rowId ? 'clickRowStyl' : '';
	}

	onReset = () => {
		this.props.form.resetFields();
	}

	// 
	updateData = (dataSource) => {
		const {rowObj} = this.state;
		if (rowObj.subExpectResources == undefined) { // 
			this.store.expectResource = Object.assign([], dataSource);
			this.store.expect["resources"] = Object.assign([], dataSource);
		} else {  // 
			let expect = JSON.parse(JSON.stringify(this.store.expect));
			const subExpects = expect.subExpects;
			const index = subExpects.findIndex(item => rowObj.id === item.id);
			const item = subExpects[index];
			let newRow = Object.assign(item, {"subExpectResources": dataSource});
			subExpects[index] = newRow;
			this.store.expect = Object.assign(expect, {"subExpects": subExpects});
		}
	}

	// 
	handleSubmit = () => {
		// 
		let expect = JSON.parse(JSON.stringify(this.store.expect));
		// 
		let resources = expect.resources == null ? [] : expect.resources.map((item) => {
			if (item.id.startsWith("!")) {
				return Object.assign(item, {id: ""});
			} else {
				return item;
			}
		});
		// 
		let subExpects = expect.subExpects == null ? [] : expect.subExpects.map((item) => {
			return Object.assign(item, {
				"subExpectResources": item.subExpectResources == null ? [] : item.subExpectResources.map((mitem) => {
					if (mitem.id.startsWith("!")) {
						return Object.assign(mitem, {id: ""});
					} else {
						return mitem;
					}
				})
			});
		});
		const params = Object.assign(expect, {
			"resources": resources,
			"subExpects": subExpects
		});

		this.store.saveExpect(params).then((res) => {
			if (res) {
				message.success("");
				this.store.getExpectById();
				let expect2 = JSON.parse(JSON.stringify(this.store.expect));
				this.setState({
					rowId: "",
					rowObj: Object.assign({})
				}, () => {
					this.store.expectResource = Object.assign([], expect2.resources == null ? [] : expect2.resources);
				});
			}
		});
	}

	// 
	handleCancle = () => {
		this.store.getExpectById();
		this.store.expectResource = [];
		this.setState({
			rowId: ""
		});
		this.onReset();
	}

	onRef = (ref) => {
		this.child = ref
	}

	updateState = (data) => {
		let expect = JSON.parse(JSON.stringify(this.store.expect));
		this.store.expect = Object.assign(expect, {"technology": data});
	}

	// 
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
			try {
				let anchorElement = document.getElementById(anchorName);
				if (anchorElement) {
					anchorElement.scrollIntoView({block: 'start'});
				}
			} catch (err) {
				console.log("scrollToAnchorERR=>" + err);
			}
		}
	}

	updateNew = (record) =>{
		this.setState({
			rowId: record.id,
			rowObj: record
		}, () => {
			const {subExpectResources} = record;
			if (subExpectResources == undefined) { // 
				let expect = JSON.parse(JSON.stringify(this.store.expect));
				this.store.expectResource = Object.assign([], expect.resources == null ? [] : expect.resources);
			} else {  // 
				this.store.expectResource = Object.assign([], record.subExpectResources == null ? [] : record.subExpectResources);
				this.onReset();
			}
		});
	}

	render() {
		let pathname = this.props.match.path;
		const {key} = this.state;
		const {expect, directoryTree, expectSource, expectResource, id} = this.store;
		const isDisable = id == "" || id == null ? true : false;
		const columns = [
			{
				title: '',
				dataIndex: 'beginTime',
				key: 'beginTime',
				editable: !isDisable,
				align: 'center',
				width: '25%'
			},
			{
				title: '',
				dataIndex: 'endTime',
				key: 'endTime',
				editable: !isDisable,
				align: 'center',
				width: '25%'
			},
			{
				title: '',
				dataIndex: 'quantities',
				key: 'quantities',
				editable: !isDisable,
				align: 'center',
				width: '25%'
			},
			{
				title: '',
				dataIndex: 'units',
				key: 'units',
				editable: !isDisable,
				align: 'center',
				width: '25%'
			}
		];
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
				}),
			};
		});
		const buttonLayout = {
			labelCol: {
				xs: 24,
				sm: 0
			},
			wrapperCol: {
				xs: 24,
				sm: 24
			}
		}
		return (
			<div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href="#/bpm/construction"></Breadcrumb.Item>
						<Breadcrumb.Item></Breadcrumb.Item>
					</Breadcrumb>
				</div>
			<Row className="mt10">
					<Row>
						<Col span="5" className="tc">
							<Button type="primary" onClick={this.handleAddd}></Button>
						</Col>
					</Row>
					<Col span="5">
						<LeftTree
							store={this.store}
							directoryTree={mobx.toJS(directoryTree)}
							onReset={this.onReset}
							onClickRow={this.updateNew}
							callback={this.callback}
						/>
					</Col>
					<Col span="19" style={{borderLeft: '1px solid #EBEBEB', minHeight: '350px', paddingLeft: '5px'}}>
						<Tabs activeKey={key} onChange={this.callback} className="schedule_tabs">
							<TabPane tab="" key="1"/>
							<TabPane tab="" key="2"/>
							<TabPane tab="" key="3"/>
						</Tabs>
						{key != "3" ? <ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table
								components={components}
								columns={column}
								dataSource={mobx.toJS(expectSource)}
								pagination={false}
								bordered={true}
								className="mb15"
								onRow={this.onClickRow}
								rowClassName={this.setRowClassName}
								rowKey={record => record.id}
							/>
							<div id="ziyuan">
								<ProEditTable
									dataSource={mobx.toJS(expectResource)}
									updateData={this.updateData}
									onRef={this.onRef}
									disabled={isDisable}
								/></div>
						</ConfigProvider> : <Technological
							disabled={isDisable}
							store={this.store}
							expect={mobx.toJS(expect)}
							updateState={this.updateState}
						/>}
						<Row>
							<Col span="24" className="tc" style={{'margin': '30px 0'}}>
								<Form.Item  {...buttonLayout}>
									<Button
										disabled={isDisable}
										type="primary"
										onClick={this.handleSubmit}
										style={{
											'padding': '24px 32px',
											'lineHeight': '0',
											'fontSize': '16px'
										}}></Button>
									<Button
										disabled={isDisable}
										type="default"
										onClick={this.handleCancle}
										style={{
											'marginLeft': '30px',
											'padding': '24px 32px',
											'lineHeight': '0',
											'fontSize': '16px'
										}}></Button>
								</Form.Item>
							</Col>
						</Row>
					</Col>
				</Row>
			</div>
		)
	}
}


const Index = Form.create({})(Schedule);
export default Index;



