/*
* 
* */
import React from 'react';
import {
	Form,
	Input,
	Icon,
	Table,
	Popconfirm,
	ConfigProvider,
	InputNumber,
	Select
} from 'antd';
import {v4 as uuidv4} from 'uuid';

const {Option} = Select;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			if (values.type !== "") {
				values.spec = "";
				values.manufacturer = "";
			}
			handleSave({...record, ...values});
		});
	};

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'quantity') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							defaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex]}
							ref={node => (this.input = node)}
							onChange={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'type') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Select
							style={{width: '100%'}}
							ref={node => (this.input = node)}
							dafaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex]}
							onChange={this.save}
							onSelect={this.save}
							onBlur={this.save}>
							<Option value=""></Option>
							<Option value=""></Option>
							<Option value=""></Option>
						</Select>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'name') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							defaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex]}
							ref={node => (this.input = node)}
							onChange={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
				/*<div className="pr">


						</div>*/
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else {
			if (record["type"] == "") {
				return <div>
					<Form.Item
						labelCol={{
							xs: {span: 24},
							sm: {span: 6},
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 16},
						}}
						label=""
						style={{margin: 0}}>
						{form.getFieldDecorator("spec", {
							rules: [{
								required: false,
								message: `${title}`,
							}],
							initialValue: record["spec"] == null || record["spec"] == 0 ? "" : record["spec"],
						})(
							<Input
								dafaultValue={record["spec"] == null || record["spec"] == 0 ? "" : record["spec"]}
								ref={node => (this.input = node)}
								onChange={this.save}
								onBlur={this.save}
							/>
						)}
					</Form.Item>
					<Form.Item
						labelCol={{
							xs: {span: 24},
							sm: {span: 6},
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 16},
						}}
						label=""
						style={{margin: 0}}>
						{form.getFieldDecorator("manufacturer", {
							rules: [{
								required: false,
								message: `${title}`,
							}],
							initialValue: record["manufacturer"] == null || record["manufacturer"] == 0 ? "" : record["manufacturer"],
						})(
							<Input
								dafaultValue={record["manufacturer"] == null || record["manufacturer"] == 0 ? "" : record["manufacturer"]}
								ref={node => (this.input = node)}
								onChange={this.save}
								onBlur={this.save}
							/>
						)}
					</Form.Item>
				</div>
			} else {
				return editing ? (<Form.Item style={{margin: 0}}>
						{form.getFieldDecorator(dataIndex, {
							rules: [{
								required: false,
								message: `${title}`,
							}],
							initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
						})(
							<Input
								dafaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex]}
								ref={node => (this.input = node)}
								onChange={this.save}
								onBlur={this.save}
							/>
						)}
					</Form.Item>
				) : (<div
						className="editable-cell-value-wrap"
						style={{minHeight: '31px'}}
					>
						{children}
					</div>
				);
			}
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

class ProEditTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			dataSource: [],
			addData: {
				id: "",
				type: "",
				name: "",
				units: "",
				content: "",
				quantity: "",
				spec: "",
				manufacturer: ""
			},
		}
	}

	componentWillMount() {

	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (JSON.stringify(nextProps.dataSource) != JSON.stringify(this.props.dataSource)) {
			this.setState({
				dataSource: nextProps.dataSource
			});
		}
	}

	onReset = () => {
		this.props.form.resetFields();
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource
		});
		this.props.onRef(this);
	}

	// 
	handleAdd = () => {
		const {dataSource, addData} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		const newData = Object.assign(addData, {id: "!" + uuidv4()});
		console.log(newData, '')
		this.setState({
			dataSource: [...source, newData],
		}, () => {
			console.log(this.state.dataSource, 'all')
			this.props.updateData(this.state.dataSource);
		});
	}

	// 
	handleDelete = (record) => {
		const {id} = record;
		console.log(record, '')
		const {dataSource} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		this.setState({
			dataSource: source.filter(item => item.id !== id)
		}, () => {
			this.props.updateData(this.state.dataSource);
		});
	};

	// 
	handleSave = (row) => {
		const {dataSource} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		const index = source.findIndex(item => row.id === item.id);
		const item = source[index];
		let newRow = Object.assign(row, {"quantity": typeof row.quantity == "string" ? row.quantity : row.quantity.toString()});
		source.splice(index, 1, {
			...item,
			...newRow,
		});
		this.setState({dataSource: source}, () => {
			this.props.updateData(this.state.dataSource);
		});
	}

	render() {
		const {dataSource} = this.state;
		const {disabled} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
				editable: !disabled,
			},
			{
				title: '',
				dataIndex: 'type',
				key: 'type',
				align: 'center',
				editable: !disabled,
			},
			{
				title: '',
				dataIndex: 'quantity',
				key: 'quantity',
				align: 'center',
				editable: !disabled,
			},
			{
				title: '',
				dataIndex: 'content',
				key: 'content',
				align: 'center',
				editable: !disabled,
			},
			{
				title: '',
				dataIndex: 'app',
				key: 'app',
				align: 'center',
				width: '60px',
				fixed: 'right',
				render: (text, record) => {
					return <Popconfirm title="?" onConfirm={() => disabled?null:this.handleDelete(record)}>
						<Icon
							style={{
								margin: '16px 0',
								color: '#4BA4BE',
								cursor: 'pointer',
								fontSize: '18px'
							}}
							type="minus-circle"/>
					</Popconfirm>
				}
			},
		];
		const colu = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
				}),
			};
		});

		return (
			<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="mt10"
					pagination={false}
					columns={colu}
					size={"middle"}
					bordered={true}
					dataSource={dataSource}
					rowKey={record => record.id}
				/>
				<Icon
					className={disabled?"hidden":""}
					onClick={this.handleAdd}
					style={{margin: '16px 0', color: '#4BA4BE', fontSize: '18px'}}
					type="plus-circle"/>
			</ConfigProvider>)
	}
}

const Index = Form.create({})(ProEditTable);
export default Index;





