import React from 'react';
import {observer} from 'mobx-react';
import {Tree, Input, message, Popover, Modal, Form, Spin, Select, AutoComplete} from 'antd'
import {observable} from "mobx";

const {TreeNode, DirectoryTree} = Tree;
const {confirm} = Modal;

@observer
class DingtalkTree extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			vis: false,
			dataRef: {}, // 
			fetching: false,
			text: "", //
			pageIndex: 1,
			companyData: [],
			expandedKeys: [],
			selectedKeys: []
		}
	}

	componentDidMount() {
		this.setState({
			text: "", //
			pageIndex: 1,
		}, () => {
			this.getCompanyList(); // api
		})
	}

	setCheckNum = (value, e) => {
		const {val} = e.props;
		let data = JSON.parse(JSON.stringify(this.state.dataRef));
		const param = Object.assign(data, {name: val.taskName, taskId: val.id})
		this.setState({
			dataRef: param
		});
	}

	companyScroll = e => {
		e.persist();
		const {target} = e;
		if (target.scrollTop + target.offsetHeight === target.scrollHeight) {
			const {pageIndex, text} = this.state;
			const nextScrollPage = pageIndex + 1;
			this.setState({
				pageIndex: nextScrollPage
			}, () => {
				const {text, pageIndex, companyData} = this.state;
				const old = JSON.parse(JSON.stringify(companyData))
				const params = {
					pageIndex,
					text
				}
				this.store.getTaskPage(params).then((res) => {
					this.setState({
						companyData: [...old, ...res.body]
					});
				});
			});
		}
	};

	fetchCompany = value => {
		this.setState({companyData: [], text: value == undefined ? "" : value, fetching: true, pageIndex: 1}, () => {
			this.getCompanyList(); // api
		});
	};

	getCompanyList = () => {
		const {pageIndex, text} = this.state;
		const params = {
			pageIndex,
			text
		}
		this.store.getTaskPage(params).then((res) => {
			this.setState({
				companyData: Object.assign([], typeof (res.body) !== "undefined" && res.body !== null ? res.body : [])
			});
		});
	}

	handleOk = e => {
		e.preventDefault();
		this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				const {dataRef} = this.state;
				let data = JSON.parse(JSON.stringify(dataRef));
				let param;
				if (data.isStandard == null || data.isStandard == false) {  //
					param = Object.assign(data, {name: values["name"]})
				} else {
					param = data;
				}
				this.store.saveDirectoryTree(param).then((res) => {
					if (res) {
						message.success("");
						this.store.getDirectoryTreeByProjectId();
						this.props.form.resetFields();
						this.setState({
							visible: false,
						});
						if (data.parentId !== null) {
							this.onExpand([data.parentId])
						}
					}
				});
			}
		});
	};

	handleCancel = e => {
		this.props.form.resetFields();
		this.setState({
			visible: false,
		});
	};

	// 
	onRightClick = (dataRef) => {
		let _this = this;
		//const {props} = event.node;
		//const {dataRef} = props;
		_this.setState({
			visible: true,
			dataRef: Object.assign({}, dataRef)
		});
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			okText: '',
			cancelText: '',
			title: '?',
			content: ' ',
			onOk() {
				_this.store.delDirectoryTree(param.id)
				.then((res) => {
					if (res) {
						message.success("");
						_this.store.getDirectoryTreeByProjectId();

						if (param.parentId !== null) {
							_this.onExpand([param.parentId])
						}

						_this.setState({
							selectedKeys: []
						});
						_this.props.callback("1");

						// 
						_this.store.id = "";
						_this.store.expect = Object.assign({}, {
							"id":null,
							"task":null,
							"name":null,
							"projectId":null,
							"quantities":null,
							"units":null,
							"technology":null,
							"technologyId":null,
							"beginTime":0,
							"endTime":0,
							"remark":null,
							"resources":null,
							"subExpects":null
						});
						_this.store.expectSource = Object.assign([]);
						_this.store.expectResource = Object.assign([]);
					}
				});
			},
			onCancel() {
				if (param.parentId !== null) {
					_this.onExpand([param.parentId])
				}
			},
		});
	}

	// 
	createNode = (item, type) => {
		let param = {};
		if (type == "1") {  // 
			if (item.parentId == null) {
				message.warn("");
				return false;
			}
			param = Object.assign(item, {name: ""});
		} else if (type == "2") { // 
			if (item.id == null) {
				message.warn("");
				return false;
			}
			param = Object.assign(item, {id: null, parentId: item.id, name: ""});
		} else if (type == "3") { // 
			delete item.children;
			param = Object.assign(item, {id: null, parentId: item.parentId, name: ""});
		} else { // 
			param = Object.assign({}, item, {id: null, parentId: item.id, name: ""});
		}
		if (type == "4") {  // 
			this.setState({
				vis: true,
				dataRef: Object.assign({}, param)
			});
		} else {
			this.store.saveDirectoryTree(param).then((res) => {
				if (res) {
					message.success("");
					this.store.getDirectoryTreeByProjectId();
					if (item.id !== null) {
						this.onExpand([item.id])
					}
				}
			});
		}
	}

	renderTreeNodes = data =>
		data.map(item => {
			if (item.children) {
				return (
					<TreeNode title={item.expectId == null || item.expectId == "" ? <Popover placement="right"
					                                                                         content={<div>
						                                                                         <p className={item.parentId == null ? "hidden" : "pointer schedule_node"}
						                                                                            onClick={this.createNode.bind(this, item, '1')}></p>
						                                                                         <p className="pointer schedule_node"
						                                                                            onClick={this.createNode.bind(this, item, '2')}></p>
						                                                                         <p className="pointer schedule_node"
						                                                                            onClick={this.createNode.bind(this, item, '3')}></p>
						                                                                         <p className="pointer schedule_node"
						                                                                            onClick={this.createNode.bind(this, item, '4')}></p>
						                                                                         <p className="pointer schedule_node"
						                                                                            onClick={this.handleDel.bind(this, item)}></p>
						                                                                         <p className="pointer schedule_node"
						                                                                            onClick={this.onRightClick.bind(this, item)}></p>
					                                                                         </div>}>
						{item.name}
					</Popover> : <Popover placement="right"
					                      content={<div>
						                      <p className="pointer schedule_node"
						                         onClick={this.handleDel.bind(this, item)}></p>
						                      <p className="pointer schedule_node"
						                         onClick={this.onRightClick.bind(this, item)}></p>
					                      </div>}>
						<span>{item.name} {item.isStandard ? <span className="schedule_span"></span> :
							<span className="schedule_span"></span>}</span>
					</Popover>} key={item.id} dataRef={item}>
						{this.renderTreeNodes(item.children)}
					</TreeNode>
				);
			}
			return (<TreeNode title={item.expectId == null || item.expectId == "" ? <Popover placement="right"
			                                                                                 content={<div>
				                                                                                 <p className={item.parentId == null ? "hidden" : "pointer schedule_node"}
				                                                                                    onClick={this.createNode.bind(this, item, '1')}></p>
				                                                                                 <p className="pointer schedule_node"
				                                                                                    onClick={this.createNode.bind(this, item, '2')}></p>
				                                                                                 <p className="pointer schedule_node"
				                                                                                    onClick={this.createNode.bind(this, item, '3')}></p>
				                                                                                 <p className="pointer schedule_node"
				                                                                                    onClick={this.createNode.bind(this, item, '4')}></p>
				                                                                                 <p className="pointer schedule_node"
				                                                                                    onClick={this.handleDel.bind(this, item)}></p>
				                                                                                 <p className="pointer schedule_node"
				                                                                                    onClick={this.onRightClick.bind(this, item)}></p>
			                                                                                 </div>}>
					<span>{item.name}</span>
				</Popover> : <Popover placement="right"
			                          content={<div>
				                          <p className="pointer schedule_node"
				                             onClick={this.handleDel.bind(this, item)}></p>
				                          <p className="pointer schedule_node"
				                             onClick={this.onRightClick.bind(this, item)}></p>
			                          </div>}>
						<span>{item.name} {item.isStandard ? <span className="schedule_span"></span> :
							<span className="schedule_span"></span>}</span>
				</Popover>} key={item.id} {...item}
			                  dataRef={item}></TreeNode>
			)
		});

	onSelect = (keys, event) => {
		const {props} = event.node;
		const {dataRef} = props;
		const {id, expectId} = dataRef;
		if (expectId == null) {
			//message.warn("");
			return false;
		} else {
			this.props.onReset();
			this.props.form.resetFields();
			this.setState({
				selectedKeys: keys
			});
			this.store.id = expectId;
			this.store.getExpectById().then((res) => {
				let obj = {
					"id": res.id,
					"quantities": res.quantities,
					"units": res.units,
					"beginTime": res.beginTime,
					"endTime": res.endTime
				};
				this.props.onClickRow(obj);
			});
		}
	};

	handleChange = (value, e) => {
		if (value === undefined) return false;
		const {val} = e.props;
		let data = JSON.parse(JSON.stringify(this.state.dataRef));
		let param;
		if (val == undefined) {
			param = Object.assign(data, {name: value, taskId: ""})
		} else {
			param = Object.assign(data, {name: val.taskName, taskId: val.id})
		}
		this.setState({
			dataRef: param
		});
	}

	handleCal = () => {
		this.setState({
			vis: false
		});
	}

	handleOk2 = () => {
		let data = JSON.parse(JSON.stringify(this.state.dataRef));
		this.store.saveExpectDirectoryTree(data).then((res) => {
			if (res) {
				message.success("");
				this.store.getDirectoryTreeByProjectId();
				if (data.parentId !== null) {
					this.onExpand([data.parentId])
				}
				this.setState({
					selectedKeys: [res.id]
				});

				// 
				const {expectId} = res;
				this.store.id = expectId;
				this.store.getExpectById().then((res) => {
					let obj = {
						"id": res.id,
						"quantities": res.quantities,
						"units": res.units,
						"beginTime": res.beginTime,
						"endTime": res.endTime
					};
					this.props.onClickRow(obj);
				});
				this.props.onReset();

				this.handleCal();
			}
		});
	}

	onExpand = (keys) => {
		this.setState({
			expandedKeys: Object.assign([], keys)
		});
	}

	render() {
		const {directoryTree} = this.props;
		const {visible, dataRef, fetching, companyData, vis, expandedKeys, selectedKeys} = this.state;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const isShow = dataRef && (dataRef.expectId == null || dataRef.expectId == "") || (dataRef.expectId !== "" && (dataRef.isStandard == false || dataRef.isStandard == null)) ? true : false;
		return <div>
			<DirectoryTree
				onSelect={this.onSelect}
				selectedKeys={selectedKeys}
				//onRightClick={this.onRightClick}
				onExpand={this.onExpand}
				expandedKeys={expandedKeys}
				defaultExpandAll
			>{this.renderTreeNodes(directoryTree)}</DirectoryTree>
			<Modal
				title=""
				visible={visible}
				onOk={this.handleOk}
				onCancel={this.handleCancel}
			>
				<Form {...formItemLayout}>
					{isShow ? <Form.Item label="">
						{getFieldDecorator('name', {
							initialValue: dataRef.name,
							rules: [
								{
									required: true,
									message: '',
								},
							],
						})(<Input/>)}
					</Form.Item> : <Form.Item label="">
						<div id="companyDiv" style={{position: 'relative'}}>
							{getFieldDecorator('name33', {
								initialValue: dataRef.name,
								rules: [
									{
										required: true,
										message: '',
									},
								],
							})(<Select
									showSearch
									placeholder=""
									notFoundContent={fetching ? <Spin size="small"/> : null}
									onSearch={this.fetchCompany}
									onChange={this.setCheckNum}
									onPopupScroll={this.companyScroll}
									filterOption={false}
									getPopupContainer={() => document.getElementById('companyDiv')}
								>
									{companyData && companyData.map(item => {
										return (
											<Option value={item.taskName} key={item.id} title={item.taskName} val={item}>
												{item.taskName}
											</Option>)
									})}
								</Select>
							)}
						</div>
					</Form.Item>}
				</Form>
			</Modal>
			{/**/}
			<Modal
				title=""
				visible={vis}
				onOk={this.handleOk2}
				onCancel={this.handleCal}
			>
				<Form {...formItemLayout}>
					<Form.Item label="">
						<div id="companyDiv2" style={{position: 'relative'}}>
							{getFieldDecorator('name55', {
								initialValue: dataRef.name,
								rules: [
									{
										required: true,
										message: '',
									},
								],
							})(<AutoComplete
									showSearch
									placeholder=""
									notFoundContent={fetching ? <Spin size="small"/> : null}
									onSearch={this.fetchCompany}
									onChange={this.handleChange}
									onSelect={this.handleChange}
									onPopupScroll={this.companyScroll}
									filterOption={false}
									getPopupContainer={() => document.getElementById('companyDiv2')}
								>
									{companyData && companyData.map(item => {
										return (
											<AutoComplete.Option value={item.taskName} key={item.id} title={item.taskName}
											                     val={item}>
												{item.taskName}
											</AutoComplete.Option>)
									})}
								</AutoComplete>
							)}
						</div>
					</Form.Item>
				</Form>
			</Modal>
		</div>
	}
}

const leftTree = Form.create({})(DingtalkTree);
export default leftTree;



