/*
*  store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from 'utils/api';
import Schemas from "@/utils/Schemas/xmgl";

// json
function transFormTree(data) {
	let result = [];
	if (!Array.isArray(data)) {
		return result
	}
	let map = {};
	data.forEach(item => {
		map[item.id] = item;
	});
	data.forEach(item => {
		let parent = map[item.parentId];
		if (parent) {
			(parent.children || (parent.children = [])).push(item);
		} else {
			result.push(item);
		}
	});
	return result;
}

class ScheduleStore {
	@observable projectId = "";
	@observable parojectName = "";
	@observable directoryTree = []; // 
	@observable expect = {
		"id":null,
		"task":null,
		"name":null,
		"projectId":null,
		"quantities":null,
		"units":null,
		"technology":null,
		"technologyId":null,
		"beginTime":0,
		"endTime":0,
		"remark":null,
		"resources":null,
		"subExpects":null
	}; // 
	@observable expectSource = [];  // 
	@observable expectResource = [];  // 
	@observable id = "";  // id

	// ID
	@action
	getDirectoryTreeByProjectId() {
		return requestPost('getDirectoryTreeByProjectId', Config.xmgl.getDirectoryTreeByProjectId, Schemas.xmgl.getDirectoryTreeByProjectId, {projectId: this.projectId})
		.then((res) => {
			if (Array.isArray(res.data)) {
				const treeData = transFormTree(res.data);
				this.directoryTree = Object.assign([], treeData);
			}
		});
	}

	// ID
	@action
	addDirectoryTree() {
		return requestPost('addDirectoryTree', Config.xmgl.addDirectoryTree, Schemas.xmgl.addDirectoryTree, {
			projectId: this.projectId
		});
	}

	// 
	@action
	saveDirectoryTree(param) {
		return requestPost('saveDirectoryTree', Config.xmgl.saveDirectoryTree, Schemas.xmgl.saveDirectoryTree, {directoryTree:param})
	}

	// 
	@action
	saveExpectDirectoryTree(param) {
		return requestPost('saveExpectDirectoryTree', Config.xmgl.saveExpectDirectoryTree, Schemas.xmgl.saveExpectDirectoryTree, {directoryTree:param})
	}

	// 
	@action
	getExpectById() {
		return requestPost('getExpectById', Config.xmgl.getExpectById, Schemas.xmgl.getExpectById, {id:this.id})
		.then((res) => {
			let data = [];
			let obj = {"id": res.id, "quantities": res.quantities,"units": res.units,"beginTime":res.beginTime,"endTime":res.endTime};
			data.push(obj);
			let subExpects = res.subExpects==null?[]:res.subExpects;
			this.expectSource = [...data, ...subExpects];
            this.expect = Object.assign(res, {
	            resources: res.resources==null?[]:res.resources,
	            subExpects: res.subExpects==null?[]:res.subExpects
            });
            return new Promise((resolve)=>{
            	resolve(res);
            });
		});
	}

	// 
	@action
	saveExpect(params) {
		return requestPost('saveExpect', Config.xmgl.saveExpect, Schemas.xmgl.saveExpect, {expect: params})
	}

	// 
	@action
	delDirectoryTree(params) {
		return requestPost('delDirectoryTree', Config.xmgl.delDirectoryTree, Schemas.xmgl.delDirectoryTree, {id: params})
	}

    // 
	@action
	getTaskPage(params) {
		return requestPost('getTaskPage', Config.xmgl.getTaskPage, Schemas.xmgl.getTaskPage, {text: params.text, pageIndex: params.pageIndex, pageSize:10})
	}
}

export default ScheduleStore;



